import { Action } from '@ali/4ever-cangjie';
import Table from '../../mo/models';

export const TABLE_MOVE_ROWS = Symbol('TABLE_MOVE_ROWS');

export function moveTableRows(
  table: Table,
  originIndexRange: {
    start: number;
    end: number;
  },
  targetIndex: number,
) {
  return {
    type: TABLE_MOVE_ROWS,
    payload: {
      node: table,
      originIndexRange,
      targetIndex,
    },
  };
}

interface TableMoveRowsPayload {
  node: Table,
  targetIndex: number;
  originIndexRange: {
    start: number;
    end: number;
  },
}

interface TableMoveRowsAction {
  type: typeof TABLE_MOVE_ROWS;
  payload: TableMoveRowsPayload;
}

export function isTableMoveRowsAction(
  action: Action,
): action is TableMoveRowsAction {
  return action.type === TABLE_MOVE_ROWS;
}
